<template>
	<div id="app">
		<introduction-modal
			v-if="displayModal == true"
			v-model="username"
			@add:username="updatedUsername"
		/>
		<p id="app-header">Mr. Task Guy</p>
		<span id="user-display" @click="displayModal = true">Current user: {{ this.username }}</span>
		<nav>
			<router-link to="/">Home</router-link>
			<router-link to="/about">Library</router-link>
		</nav>
		<router-view></router-view>
	</div>
</template>

<script>
	import IntroductionModal from "./components/IntroductionModal.vue";

	export default {
		name: "app",
		components: {
			IntroductionModal
		},
		data() {
			return {
				username: '',
				userDisplay: "Current user: " + this.username,
				displayModal: true
			};
		},

		methods: {
			updatedUsername(username) {
				console.log("event works!");
				this.username = username;

				localStorage.setItem("taskGuyUsername", this.username);
			},
			checkForExistinguser() {
				let existingUser = localStorage.getItem("taskGuyUsername");

				if (existingUser) {
					this.username = existingUser;
					this.displayModal = false;
				} else {
					this.displayModal = true;
				}
			}
		},

		mounted() {
			this.checkForExistinguser();
		}
	};
</script>

